.nav {
  &__list {
    display: flex;
    flex-wrap: nowrap;
  }

  &__item {
    margin-right: 27px;
    font-weight: 700;
    font-size: 15px;
  }

  &__link {
    color: inherit;
    transition: color 0.2s ease;

    &:hover {
      color: $color-button-hover;
    }
  }

  @include tablet {
    position: absolute;
    left: 0;
    right: 0;
    top: var(--header-height);
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    min-height: calc(100vh - var(--header-height));
    background: #f1f3ef;
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;

    &__list {
      flex-direction: column;
      align-items: center;
    }

    &__item {
      margin-bottom: 40px;
      font-size: 32px;
    }

    &.menu--active {
      opacity: 1;
      pointer-events: all;
    }
  }
}
